html {
    font-family: $font-primary;
    font-size: $font-size;
    scroll-behavior: smooth;
}

body{
    background-color: $bg-body;
    color: $color-text;
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    overflow-x: hidden;

}

section{
    margin-bottom: $section-margin;
}

a {
    &:hover{
        color: $secondary-color;
    }
}

.btn-arrow{
    --size: 84px;
    --icon-size:35px;
    --border-radius:29px;
    --border-width:3px;
    width: calc(var(--size) - var(--border-width) * 2 );
    height: calc(var(--size) - var(--border-width) * 2 );
    border-radius: var(--border-radius);
    background-color:$bg-footer;
    padding: 25px;
    position: relative;
    @include flex-centered;

    &:before {
        content: "";
        position: absolute;
        background: $bg-gradient;
        width: calc(100% + var(--border-width));
        height: calc(100% + var(--border-width));
        border-radius: calc(var(--border-radius) + 1px);
        z-index: -1;
        left: 50%;
        top: 50%;
        transform: translate(-50%, -50%);
    }
}

.icon-svg{
    height: var(--icon-size);
    width: var(--icon-size);
}

.logo {
    max-width: 195px;
}

.text-gradient{
    background-clip: text;
    color: transparent;
    background-image: $bg-gradient;
}

.container{
    max-width: 1278px;
    padding-left: 12px;
    padding-right: 12px;
    margin-left: auto;
    margin-right: auto;
}

.fw-400 {
    font-weight: 400;
}

.fw-500 {
    font-weight: 500;
}

.fw-600 {
    font-weight: 600;
}

.fw-800 {
    font-weight: 800;
}

/*!important*/
.show{
    display: block !important;
}

.d-none{
    display: none !important;
}

.d-block{
    display: block !important;
}

.d-flex{
    display: flex !important;
}
/**/

.svg-sprite{
    visibility: hidden;
    height: 0;
    width: 0;
}



::selection {
    color: $primary-color;
    background-color: $bg-footer;
}